import {mapState,mapMutations} from 'vuex'
export default {
	data() {
		return {
			sessionId:'',
			myData:[]
		}
	},
	onLoad() {
		this.init()
	},
	computed: mapState(['hasLogin','uerInfo']), 
	methods: {
		...mapMutations(['login']),
		// 页面初始化
		init(){
			uni.getStorage({
				key: 'userInfo',
				success:(res) => {
					this.login(res.data);
					this.sessionId = res.data.sessionId
					uni.request({
						url:'https://www.nssnail.com/wx/personal/getOrderRecord',
						data:{
							sessionId:this.sessionId
						},
						success: (res) => {
							if(res.data.stat == 1){
								this.myData = res.data.data
								this.myData.forEach((item)=>{
									if(item.stat == 1){
										item.stat = '未付款'
									}else if(item.stat == 2){
										item.stat = '已付款(未发货)'
									}else if(item.stat == 3){
										item.stat = '失效'
									}else if(item.stat == 4){
										item.stat = '已发货'
									}
								})
							}else if(res.data.stat == 3 || res.data.stat == 4){
								uni.showModal({
									title: '提示',
									content: '登录过期，请重新登录',
									success: function (res) {
										if (res.confirm) {
											uni.navigateTo({
												url:'../myInit/myInit'
											})
										}
									}
								});
							}
						}
					})
				} 
			})
		},
		
		// 跳转至详情页面
		gotoDeatil(item){
			var id = item.id
			uni.navigateTo({
				url:'../recordDetail/recordDetail?id='+id+'&sessionId='+this.sessionId
			})
		}
	}
}
